<template>
<div class="container">
  <div class="item">
    <div class="item__link"><router-link to="/jsonplaceholder/users/" class="item__link-exit">&lt-- Назад</router-link></div>
    
    <div class="item__box" v-if="itemInfo">
        <div class="item__box-info">
        <h3><span>Имя:</span> {{itemInfo.name}}</h3>
        <p><span>Имя пользователя:</span> {{itemInfo.username}}</p>
        <p><span>Телефон:</span> {{itemInfo.phone}}</p>
        <p><span>Почта:</span> {{itemInfo.email}}</p>
        <p><span>Адрес:</span> {{itemInfo.address.street}}</p>
        <p><span>Website:</span> {{itemInfo.website}}</p>
        <p><span>Company:</span> {{itemInfo.company.name}}</p>
    </div>
    </div>
    
  </div>
  </div>
</template>

<script>
    import { mapState } from "vuex";
export default {
    data(){
        return {
            itemInfo: null
        }
    },
    computed:{
        ...mapState(['allUsers'])
    },
    mounted(){
        this.itemInfo = this.allUsers.find((item)=>item.id==this.$route.params.id)
    }
}
</script>

